/* 3 причины */
.block-rision {
  padding: 5px 5px 20px 5px; }

.rision-ico {
  margin: 7px auto 35px auto;
  padding: 0;
  width: 100px;
  height: 100px; }

.rision-ico img {
  width: 100px;
  height: 100px;
  border-radius: 10em;
  text-align: center;
  border: 2px solid #000; }

.rision-title {
  font-size: 1.125em;
  text-align: center;
  font-weight: 600;
  margin: 20px 0 30px 0; }

.rision-text {
  color: #aaaaaa;
  font-family: "LatoLight", sans-serif; }

.rision-text p {
  font-family: "LatoLight", sans-serif;
  font-size: 16px;
  line-height: 1.375;
  text-align: justify;
  margin: 0 5px 20px 5px; }

/*блок wells*/
.block-wells {
  padding: 30px 0;
  background-color: #e8776b; }

.wells {
  margin-top: 10px;
  font-size: 1.500em;
  color: #fff; }

.well-button {
  padding-right: 5px; }

.well-button a {
  text-align: center;
  display: block;
  font-size: .9em;
  padding: 18px 25px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 10px;
  transition: all .75s ease; }

.well-button a:hover {
  box-shadow: inset 0 0 5px 1px #fff; }

/* блок с маком*/
.block-mac {
  background-color: #5e95cd; }

.block-mac img {
  width: 100%;
  height: auto; }

.block-mac .rision-title, .block-mac .rision-text {
  color: #fff; }

.block-mac .rision-title {
  margin: 0; }

.block-mac .rision-text {
  margin-top: 10px; }

/* блок с ценами */
.block-price .col-md-4 {
  margin: 0;
  padding: 0; }

.block-price {
  text-align: center; }

.price {
  width: 100%;
  border: 1px solid #e1e1e1; }

.price-title {
  background-color: #e1e1e1;
  color: #999999;
  font-size: 1.125em;
  padding: 20px 0; }

.price-number {
  color: #e8776b;
  font-size: 1.875em;
  padding: 40px 0; }

.price-color {
  background-color: #eeeeee; }

.price-w {
  font-family: "LatoLight", sans-serif;
  color: #999999;
  font-size: 0.8em;
  padding: 20px 0; }

.price-best {
  background-color: #e8776b;
  color: #fff; }

.price-button {
  padding: 25px 0;
  text-align: center; }

.price-button a {
  display: inline-block;
  border-radius: 5px;
  padding: 10px 35px;
  font-size: 1em;
  background-color: #e8776b;
  text-align: center;
  color: #fff;
  border-bottom: 1px solid #e84e3c;
  transition: all .75s ease; }

.price-button a:hover {
  background-color: #e84e3c;
  border-bottom: 1px solid #e84e3c; }

/* форма подписки */
.block-sub {
  padding: 30px 0 50px;
  background: #000 url("../images/top-header.jpg") no-repeat;
  background-size: cover; }

.block-sub .wells {
  text-align: center; }

.form-calling {
  margin-top: 30px;
  font-size: .875em;
  transition: all 0.5s else; }

.form-calling input,
.form-calling .get-buttons {
  font-family: "LatoRegular", sans-serif;
  display: block;
  border-radius: 5px;
  width: 90%;
  height: 45px;
  line-height: 45px;
  background-color: #fff;
  color: #aaaaaa;
  text-indent: 10px;
  padding: 10px 0;
  margin: 10px auto;
  border: 0;
  transition: all .5s ease;
  font-size: 1.25em; }

.form-calling input:hover {
  background-color: #aaaaaa;
  color: #000; }

.form-calling .get-buttons {
  width: 90%;
  line-height: 0;
  background-color: #3581cc;
  text-align: center;
  color: #fff;
  cursor: pointer; }

.form-calling .get-buttons:hover {
  background-color: #5e95cd; }

/* начало оформления футера*/
.social-icons {
  margin: 0 auto;
  padding: 0; }

.social-icons ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center; }

.social-icons ul li {
  margin-left: 9px;
  display: inline-block; }

.social-icons ul li a {
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  color: #fff;
  line-height: 45px;
  text-align: center;
  font-size: 1em;
  border: 2px solid #fff;
  transition: all 1.25s ease; }

.social-icons ul li a:hover {
  color: #fff;
  border: 2px solid #fff;
  -moz-transform: scale(1.2);
  -webkit-transform: scale(1.2);
  transform: scale(1.2); }

/*стили кнопки наверх*/
.top {
  position: fixed;
  bottom: 20px;
  right: -120px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #15a4fa;
  cursor: pointer;
  text-align: center;
  opacity: .3;
  z-index: 3;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.top .fa {
  color: #fff;
  font-size: 2em;
  margin-top: 6px; }

.top:hover {
  opacity: 1; }

.top.active {
  right: 30px; }

