.block-one-example {
  margin: 30px 0; }

/* слайдер */
.owl-carousel .owl-dots .owl-dot.active
span {
  background-color: red; }

.owl-carousel .owl-dots .owl-dot:hover
span {
  background-color: red; }

.block-items {
  cursor: default; }

.item-pad {
  padding-bottom: 30px; }

.inner-test {
  text-align: center;
  background-color: #fff; }

.inner-test > img {
  max-width: 160px;
  max-height: 160px;
  float: left;
  border-radius: 10em;
  border: 1px solid black;
  vertical-align: bottom;
  margin: 20px 5px 20px 20px; }

h3.order-name {
  padding: 10px 0;
  font-size: 20px;
  font-weight: 900; }

p.iner-text {
  text-align: left;
  margin-left: 220px;
  padding: 15px 0 25px;
  font-size: 1.1em;
