/*Изменяем внешний вид элементов управления (точки) */
.owl-controls {
  margin-top: 30px; }

.owl-theme .owl-dots .owl-dot span {
  width: 14px;
  height: 14px;
  background-color: #EE82EE;
  -webkit-transition: background-color .3s ease;
