.owl-my-nav .owl-prev {
  left: 50px; }

.owl-my-nav .owl-next {
  right: 50px;
  text-align: right; }

.forms-calldecor input {
  height: 40px;
  line-height: 40px; }

.forms-calldecor textarea {
  height: 100px; }

.forms-calldecor .button-call .get-buttons {
  margin-top: 10px;
  line-height: 1; }

.forms-call .get-buttons {
  margin-top: 50px;
  padding: 10px 50px;
  /* 	line-height: 50px; */
  font-size: 18px;
  border: 1px solid #DC143C;
  text-decoration: none;
  background-color: #DC143C;
  border-radius: 3px;
  transition: all 0.5s ease;
  color: #fff; }

.button-call {
  text-align: center;
  margin: 0; }

.forms-call .get-buttons:focus,
.forms-call .get-buttons:hover,
.forms-call .get-buttons:active {
  background-color: transparent;
  border: 1px solid #DC143C;
  color: #DC143C; }

/* конец формы обратной связи */
/*оформление всплывающей формы а ниже и благодарности */
.forms-calldecor {
  max-width: 480px;
  position: relative;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 0.5s else;
  overflow: hidden;
  margin: auto;
  border: 1px solid #fff;
  border-radius: 1em; }

.forms-calldecor h4, .forms-calldecor h5 {
  color: #fff;
  text-align: center;
  font-size: 1.3em;
  margin: 50px 20px 0; }

.forms-calldecor h5 {
  margin-bottom: 30px;
  margin-top: 10px;
  font-size: 0.96em;
  letter-spacing: 1px; }

.formscalldecor_input {
  display: block;
  border: none;
  text-indent: 10px;
  width: 90%;
  margin: 0 auto 10px;
  background-color: #fff;
  color: #aaaaaa;
  outline: none; }

.formscalldecor_input:hover {
  background-color: #aaaaaa;
  color: #000; }

.formscalldecor_input:focus {
  background-color: #ffffff !important;
  color: #aaaaaa; }

.formscalldecor_input:focus-within {
  background-color: #ffffff !important;
  color: #aaaaaa; }

.formscalldecor_input:visited {
  background-color: #ffffff !important;
  color: #aaaaaa; }

.formscalldecor_input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px #ffffff !important;
  /* Цвет фона */
  -webkit-text-fill-color: #aaaaaa !important;
  /* цвет текста */
  color: #aaaaaa !important;
  /* цвет текста */ }

.forms-calldecor .button-call {
  float: none;
  text-align: center;
  color: #fff;
  margin-bottom: 40px; }

.forms-calldecor .button-call .get-buttons {
  display: block;
  border: none;
  cursor: pointer;
  margin: 0 auto;
  width: 210px;
  height: 40px;
  line-height: 40px;
  box-shadow: 0 1px 2px 1px #2377cc;
  border-bottom: 2px solid #2377cc;
  color: #fff;
  background-color: #5e95cd;
  transition: all .25s linear; }

.forms-calldecor .button-call .get-buttons:hover {
  border-bottom: 2px solid #1367bc;
  box-shadow: 0 1px 2px 1px #0357ac; }

.forms-calldecor .mfp-close {
  position: absolute;
  right: 15px;
  top: 15px;
  -webkit-border-radius: 3px;
  border-radius: 3px; }

.forms-calldecor .mfp-close-btn-in .mfp-close {
  color: #fff;
  font-size: 1.5em;
  font-weight: bold;
  border-radius: 3px; }

.mfp-close-btn-in .mfp-close {
  color: #ffffff; }

.forms-calldecor .success {
  background-color: #000;
  position: absolute;
  z-index: 1;
  left: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  display: flex;
  opacity: 0;
  padding-right: 20px;
  padding-left: 20px;
  transition: all 0.5s ease; }

.forms-calldecor .success.active {
  top: 0;
  opacity: 1; }

.forms-calldecor .success p {
  text-transform: uppercase;
  display: flex;
  color: #fff;
  font-size: 20px;
  margin: auto;
  text-align: center; }

/*====== "Hinge" close effect ======*/
@keyframes hinge {
  0% {
    transform: rotate(0);
    transform-origin: top left;
    animation-timing-function: ease-in-out; }
  20%, 60% {
    transform: rotate(80deg);
    transform-origin: top left;
    animation-timing-function: ease-in-out; }
  40% {
    transform: rotate(60deg);
    transform-origin: top left;
    animation-timing-function: ease-in-out; }
  80% {
    transform: rotate(60deg) translateY(0);
    opacity: 1;
    transform-origin: top left;
    animation-timing-function: ease-in-out; }
  100% {
    transform: translateY(700px);
    opacity: 0; } }

.hinge {
  animation-duration: 1s;
  animation-name: hinge; }

.mfp-with-fade .mfp-content, .mfp-with-fade.mfp-bg {
  opacity: 0;
  transition: opacity .5s ease-out; }

.mfp-with-fade.mfp-ready .mfp-content {
  opacity: 1; }

.mfp-with-fade.mfp-ready.mfp-bg {
  opacity: 0.8; }

.mfp-with-fade.mfp-removing.mfp-bg {
  opacity: 0; }

